











import Vue from 'vue'
import { mapGetters } from 'vuex'
import TopicList from '@/components/TopicList.vue';
import TopicRecords from '@/components/TopicRecords.vue';

export default Vue.extend({
  name: 'Devices',
  components: {
    TopicList,
    TopicRecords,
  },
  computed: {
    ...mapGetters([
      'selectedTopic',
    ]),
  },
  mounted() {
    this.$nextTick(async () => {
      this.$store.dispatch('refreshTopics');
    });
  },
});
